import React, {Component} from 'react';
import navStyles from './nav.module.css';

const Nav = ({siteTitle}) => {
    return(
        <nav id="nav" className={`pt2 absolute dt w-100 center ${navStyles.pos}`}>
            <div className="dtc w5 v-mid ">
                <p className="f3 fw4 grow pointer dib-ns ph3 ml3 mb3">{siteTitle}</p>
            </div>
            <div className="dtc tr ">
                <p className="f5 fw5 grow pointer dn dib-ns pr4 mb3">Mission</p>
                <p className="f5 fw5 grow pointer dn dib-ns pr4 mb3">Who</p>
            </div>
      </nav>

    );
}

export default Nav;