import React, {Component} from 'react';
import { Link } from 'gatsby';
import 'tachyons';
import typography from '../utils/typography';

import Layout from '../components/layout';
import Landing from '../components/landing/landing';
import Section from '../components/section/section';
import Section2 from '../components/section2/section2';
import Section3 from '../components/section3/section3';


class IndexPage extends Component{
  

  //setInterval to change bg image? and switch the highlight of words on splash

  componentDidMount(){
    window.addEventListener('scroll', this.handleScroll)
    this.handleScroll();
  }
    
  

  componentWillUnmount(){
    window.removeEventListener('scroll', this.handleScroll)
  }
   
  //to toggle nevbar events on scroll height
  handleScroll = (event) =>{
    const nav = document.getElementById('nav').classList; 
    window.scrollY > 97? nav.add('fixed', 'bg-black-20', 'z-max') : nav.remove('fixed', 'bg-black-20', 'z-max');
  } 

  render(){
    
    
    return(
    <Layout >
      <Landing />

      <Section/>
      <h1 className="bg-lightest-blue tc">Infographic here?</h1>
      <Section2/>
      
      <Section3/>
      <p className="center fw6 f3 lh-copy tc w-75 mh1 ">
      For most Tech Companies, you are the product and your content 
      is owned by the company.  With OurLabs, our users are owners of 
      their content and data and we are the custodians of it.      
               </p>

    </Layout>
  )
  }
  }

export default IndexPage
